/* 導覽列 */
.nav{
    background-color: #AA0601;
    padding: 16px 0;
}
.nav-container{
    width: 67.5%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
.logo a{
    display:block;
    width:80px;
    height: 48.5px;
    background:url(../images/logo-white.png);
    background-size: cover;
    background-position: center center;
    text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;
}
.menu{
    display: flex;
    justify-content: start;
    align-items: center;
}
.menu a:hover{
    text-decoration: underline;
}

.hamberger{
    display: none;
}


/* 導覽列 end */

/* footer */
.footer-bg{
    background-color: #AA0601;
    padding: 48px 0 32px 0;
}
.footer-container{
    width: 67.5%;
    margin: 0 auto;
}
.footer{
    display: flex;
    justify-content: space-between;
    align-items: start;
}
.subMenu{
    display: flex;
}
.social img{
    width: 48px;
}
.social a:hover{
    filter:brightness(80%);
}
.copyright{
    display: flex;
    justify-content: space-between;
    padding-top: 32px;
    border-top: 1px solid #fff;
}
.footer-container a:hover{
    text-decoration: underline;
}

/* footer END */

